import meterBadge from "../images/Badge meter.webp"
import meterBar from "../images/Meter bar.webp"
import redEnd from "../images/Red end.webp"
import greenEnd from "../images/Green end.webp"
import redArrow from "../images/Red arrow.webp"
import greenArrow from "../images/Green arrow.webp"

function Gamegame() {
    return (
        <div className="game">
            <div className="gameContainer">
                <div className="gameIcon"></div>
                <div className="jackpotMeter">
                    <div className="meterBadge">
                        <img className="meterBadgeImg" src={meterBadge}></img>
                        <div className="arrowContainer">
                            <img className="arrow" src={redArrow}></img>
                        </div>
                    </div>
                    <div className="meterBar">
                        <div className="gameName">Jili Crazy 777</div>
                        <div className="jackpotPercentage">Jackpot Meter: <span className="redText">90%</span></div>
                        <img src={meterBar}></img>
                        <div className="meter">
                            <div className="meterBody redMeter" style={{ width: "23.5vw" }}>90%</div>
                            <div className="meterEnd">
                                <img src={redEnd}></img>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="history">
                    <div className="historyDetails timeframe">
                        <div>10 mins</div>
                        <div>1 hr</div>
                        <div>3 hrs</div>
                        <div>6 hrs</div>
                    </div>
                    <div className="historyDetails">
                        <div>:</div>
                        <div>:</div>
                        <div>:</div>
                        <div>:</div>
                    </div>
                    <div className="historyDetails">
                        <div className="greenText">+2.37%</div>
                        <div className="greenText">+3.59%</div>
                        <div className="redText">-0.30%</div>
                        <div className="redText">-0.08%</div>
                    </div>
                </div>
            </div>
            <div className="gameContainer">
                <div className="gameIcon"></div>
                <div className="jackpotMeter">
                    <div className="meterBadge">
                        <img className="meterBadgeImg" src={meterBadge}></img>
                        <div className="arrowContainer">
                            <img className="arrow" src={greenArrow}></img>
                        </div>
                    </div>
                    <div className="meterBar">
                        <div className="gameName">Jili Crazy 777</div>
                        <div className="jackpotPercentage">Jackpot Meter: <span className="greenText">25%</span></div>
                        <img src={meterBar}></img>
                        <div className="meter">
                            <div className="meterBody greenMeter" style={{ width: "6.5vw" }}>25%</div>
                            <div className="meterEnd">
                                <img src={greenEnd}></img>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="history">
                    <div className="historyDetails timeframe">
                        <div>10 mins</div>
                        <div>1 hr</div>
                        <div>3 hrs</div>
                        <div>6 hrs</div>
                    </div>
                    <div className="historyDetails">
                        <div>:</div>
                        <div>:</div>
                        <div>:</div>
                        <div>:</div>
                    </div>
                    <div className="historyDetails">
                        <div className="greenText">+2.37%</div>
                        <div className="greenText">+3.59%</div>
                        <div className="redText">-0.30%</div>
                        <div className="redText">-0.08%</div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Gamegame