import React from "react"
import axios from 'axios';
import Chart from "react-apexcharts";
import Graphdata from "./Graphdata";

function Graph(props) {
    const [candlestickData, setCandlestickData] = React.useState(null);
    // const [candlestickData, setCandlestickData] = React.useState(Graphdata);
    const [graphData, setGraphData] = React.useState([])
    const [chartData, setChartData] = React.useState(null);

    React.useEffect(() => {
        const fetchData = async () => {
            try {
            const response = await axios.get(
                `https://api.coingecko.com/api/v3/coins/${props.id}/ohlc?vs_currency=usd&days=7&precision=full`
            );

            setCandlestickData(response.data);
            } catch (error) {
                console.error('Error fetching data:', error);
                setCandlestickData(Graphdata)
            }
        };

        fetchData();
    }, [props.id]);

    React.useEffect(() => {
        if (candlestickData) {
            const data = candlestickData;
            let savedData = [];
        
            for (let i = 0; i < data.length; i++) {
            savedData.push({
                x: new Date(data[i][0]),
                y: [parseFloat(data[i][1]).toFixed(2), parseFloat(data[i][2]).toFixed(2), data[i][3], data[i][4]]
            });
            }
        
            setGraphData(savedData);
        }
    }, [candlestickData])

    React.useEffect(() => {
        if (graphData) {
            setChartData({
                series: [{
                    data: graphData
                    }],
                    options: {
                        chart: {
                            type: 'candlestick',
                            height: 350,
                            toolbar: {
                                autoSelected: 'pan',
                                show: false
                            },
                        },
                        grid: {
                            show: true 
                        },
                    
                        xaxis: {
                            labels: {
                                show: false 
                            },
                            axisTicks: {
                                show: false 
                            },
                            tooltip: {
                                enabled: false 
                            }
                        },
                        tooltip: {
                            enabled:false
                        },
                        yaxis: {
                            labels: {
                                show: false 
                            },
                            axisTicks: {
                                show: false 
                            },
                            tooltip: {
                                enabled: false 
                            }
                        }
                    },
            })
        }
    }, [graphData])



    return (
        <>
            <div onClick={() => props.setIsShowingGraph(false)} className="row">
                <div className="graphName">{props.name}</div>
                <div className="mixed-chart">
                    {chartData &&
                        <Chart
                            options={chartData.options}
                            series={chartData.series}
                            type="candlestick" 
                            style={{ width: '90%'}}
                        />
                    }
                </div>
            </div>
        </>
    )
}

export default Graph