import React from "react"

import logo from './logo.svg';
import './App.css';
import Header from './components/Header';
import Providers from './components/Providers';
import Game from './components/Game';
import GameList from './components/Gamelist';
import Gamegame from "./components/GameGame";
import Jili from "./components/Jili";

import backToTopIcon from "./images/Top Icon.webp"
import FilterTab from "./components/FilterTab";
import CryptoIds from "./components/CryptoIds";


function App() {  
  // const [currentGameList, setCurrentGameList] = React.useState(Jili)
  const [activeGamelist, setActiveGamelist] = React.useState(GameList)
  const [showingGamelist, setShowingGamelist] = React.useState(activeGamelist)
  const [searchName, setSearchName] = React.useState("")
  const [ids, setIds] = React.useState(CryptoIds)

  const [isAtTop, setIsAtTop] = React.useState(true);

  // Search by name
  React.useEffect(() => {
    if (searchName.length > 0) {
      const filtered = activeGamelist.filter(game =>
        game.name.toLowerCase().includes(searchName.toLowerCase())
      );
      setShowingGamelist(filtered);
    } else {
      setShowingGamelist(activeGamelist)
    }
  }, [searchName])

  function handleScroll() {
    const scrollTop = window.scrollY || document.documentElement.scrollTop;

    const threshold = 100;

    setIsAtTop(scrollTop <= threshold);
  };

  React.useEffect(() => {
    window.addEventListener('scroll', handleScroll);

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  function scrollToTop() {
    window.scrollTo({
      top: 0,
      behavior: 'smooth', // Add smooth scrolling behavior if desired
    });
  }

  function chooseProvider(provider) {
    const filterGamelist = activeGamelist.filter(game => game.provider === provider)

    setShowingGamelist(filterGamelist)
  }

  return (
    <div className="App">
      <Header />
      <FilterTab 
        setSearchName={setSearchName}
        searchName={searchName}
      />
      <Providers 
        chooseProvider={chooseProvider}
      />
      {showingGamelist.map(game => {
        return (
          <Game 
            id = {ids[game.id]}
            name = {game.name}
            icon = {game.icon}
            pOne = {game.pOne}
            pTwo = {game.pTwo}
            pThree = {game.pThree}
            pFour = {game.pFour}
            setShowingGamelist={setShowingGamelist}
          />
        )
      })}
      
      <div onClick={scrollToTop} className={`backToTop ${isAtTop ? "opacityZero" : ""}`}><img src={backToTopIcon}></img></div>
    </div>
  );
}

export default App;
