import React from "react"

import meterBadge from "../images/Badge meter.webp"
import meterBar from "../images/Meter bar.webp"
import redEnd from "../images/Red end.webp"
import greenEnd from "../images/Green end.webp"
import redArrow from "../images/Red arrow.webp"
import greenArrow from "../images/Green arrow.webp"
import Graph from "./Graph"
import CryptoIds from "./CryptoIds"

function Game(props) {

    const [currentPercentage, setCurrentPercentage] = React.useState((Math.random() * 100)  + 1)
    const [barWidth, setBarWidth] = React.useState(((currentPercentage)/100)*32)
    const [isGoingUp, setIsGoingUp] = React.useState(false)
    
    // Graph
    const [isShowingGraph, setIsShowingGraph] = React.useState(false)

    // Percentage History
    const [percentOne, setPercentOne] = React.useState(props.pOne)
    const [percentTwo, setPercentTwo] = React.useState(props.pTwo)
    const [percentThree, setPercentThree] = React.useState(props.pThree)
    const [percentFour, setPercentFour] = React.useState(props.pFour)

    // Generate History
    function generatePercent() {
        return parseFloat(((Math.random() * 20) - 10).toFixed(2))
    } 

    // 4 States
    const [continuousDecline, setContinuousDecline] = React.useState(false)
    const [continuousIncrease, setContinuousIncrease] = React.useState(false)
    const [downtrend, setDowntrend] = React.useState(false)
    const [uptrend, setUptrend] = React.useState(false)

    React.useEffect(() => {
        setCurrentPercentage((Math.random() * 100)  + 1)
        setPercentOne(props.pOne)
        setPercentTwo(props.pTwo)
        setPercentThree(props.pThree)
        setPercentFour(props.pFour)
    }, [props.name])

    // Continuous Decline
    React.useEffect(() => {
        if (continuousDecline) {
            setContinuousIncrease(false)
            setDowntrend(false)
            setUptrend(false)

            const intervalID = setInterval(() => {
                setIsGoingUp(false)
                setCurrentPercentage(percentage => percentage - 0.01)
            }, 1000)
            return () => {
                clearTimeout(intervalID);
            };
        }
    }, [continuousDecline])

     // Continuous Increase
    React.useEffect(() => {
        if (continuousIncrease) {
            setContinuousDecline(false)
            setDowntrend(false)
            setUptrend(false)

            const intervalID = setInterval(() => {
                setIsGoingUp(true)
                setCurrentPercentage(percentage => percentage + 0.01)
            }, 1000)
            return () => {
                clearTimeout(intervalID);
            };
        }
    }, [continuousIncrease])

    // Downtrend 
    React.useEffect(() => {
        if (downtrend) {
            setContinuousDecline(false)
            setContinuousIncrease(false)
            setUptrend(false)

            const intervalID = setInterval(() => {
                setIsGoingUp(false)
                const random = Math.floor(Math.random() * 2)
                if (random === 0) {
                    setCurrentPercentage(percentage => percentage - 0.02)
                } else {
                    setCurrentPercentage(percentage => percentage - 0.01)
                }
            }, 1000)

            return () => {
                clearTimeout(intervalID);
            };
        }
    }, [downtrend])

    React.useEffect(() => {
        const intervalID = setInterval(() => {
            generateRandomPercentage()
        }, 120000)

        return () => {
            clearInterval(intervalID)
        }
    }, [])

    // Uptrend
    React.useEffect(() => {
        if (uptrend) {
            setContinuousDecline(false)
            setContinuousIncrease(false)
            setDowntrend(false)

            const intervalID = setInterval(() => {
                setIsGoingUp(true)
                const random = Math.floor(Math.random() * 2)
                if (random === 0) {
                    setCurrentPercentage(percentage => percentage + 0.02)
                } else {
                    setCurrentPercentage(percentage => percentage + 0.01)
                }
            }, 1000) 

            return () => {
                clearTimeout(intervalID);
            };
        }
    }, [uptrend])

    // Random State
    React.useEffect(() => {
        const getRandomState = () => Math.floor(Math.random() * 4);
    
        function updateState()  {
            const state = getRandomState();
            if (state === 0) {
                setContinuousDecline(true);
            } else if (state === 1) {
                setContinuousIncrease(true);
            } else if (state === 2) {
                setDowntrend(true);
            } else {
                setUptrend(true);
            }
        };
        
        setTimeout(() => {
            updateState();
        }, 100)
        
        const intervalID = setInterval(updateState, 180000);
    
        return () => {
            clearInterval(intervalID);
        };
    }, []);

    // Meter Bar
    React.useEffect(() => {
        setBarWidth(((currentPercentage)/100)*32)
    }, [currentPercentage, barWidth])

    function generateRandomPercentage() {
        const prevPercent = currentPercentage
        const difference = Math.random() * 15 
        const addOrSubtract = Math.floor(Math.random() * 2)
        let randomPercentage

        if (addOrSubtract === 0) {
            randomPercentage = prevPercent + difference
        } else {
            randomPercentage = prevPercent - difference
        }
        // const randomPercentage = Math.floor(Math.random() * 100)  + 1
        if (randomPercentage <= 0) {
            setCurrentPercentage(0)
        } else if (randomPercentage >= 100) {
            setCurrentPercentage(100)
        } else {
            setCurrentPercentage(randomPercentage)
        }
        
        if (prevPercent < randomPercentage) {
            setIsGoingUp(true)
        } else {
            setIsGoingUp(false)   
        }
    }

    function checker () {
        const state = Math.floor(Math.random() * 4)
        console.log(state)
    }
    //  Check if current percentage is below 9 or greater than 95 
    React.useEffect(() => {
        if (currentPercentage > 95) {
            const randomLowPercentage = (Math.random()* 41) + 10
            setIsGoingUp(false)
            setCurrentPercentage(randomLowPercentage)
        } else if (currentPercentage < 9) {
            setIsGoingUp(true)
            const randomHighPercentage = (Math.random()* 31) + 20
            setCurrentPercentage(randomHighPercentage)
        }
    }, [currentPercentage])

    return (
        <div className="game">
            {/* <div className="buttons">
                <div><button onClick={() => setContinuousDecline(true)}>Continuous Decline</button></div>
                <div><button onClick={() => setContinuousIncrease(true)}>Continuous Increase</button></div>
                <div><button onClick={() => setDowntrend(true)}>Downtrend</button></div>
                <div><button onClick={() => setUptrend(true)} >Uptrend</button></div>
                <div><button onClick={generateRandomPercentage}>Random Percentage</button></div>
                <div><button onClick={checker}>Checking</button></div>
            </div> */}
            <div className="gameContainer">
                <div className="gameIcon"><img src={props.icon}></img></div>
                <div className="jackpotMeter">
                    <div className="meterBadge">
                        <img className="meterBadgeImg" src={meterBadge}></img>
                        <div className="arrowContainer">
                            <img className={`arrow ${isGoingUp ? "" : "reverseArrow"}`} src={!isGoingUp ? redArrow : greenArrow}></img>
                        </div>
                    </div>
                    <div onClick={() => setIsShowingGraph(true)} className="meterBar">
                        <div className="gameName">{props.name}</div>
                        <div className="jackpotPercentage">Jackpot Meter: <span className={`${!isGoingUp ? "redText" : "greenText" }`}>{parseFloat(currentPercentage.toFixed(2))}%</span></div>
                        <img src={meterBar}></img>
                        <div className="meter">
                            <div className={`meterBody ${!isGoingUp ? "redMeter" : "greenMeter"}`} style={{ width: `${barWidth}vw` }}>{parseFloat(currentPercentage.toFixed(2))}%</div>
                            <div className="meterEnd">
                                <img src={!isGoingUp ? redEnd : greenEnd}></img>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="history">
                    <div className="historyDetails timeframe">
                        <div>10 mins</div>
                        <div>1 hr</div>
                        <div>3 hrs</div>
                        <div>6 hrs</div>
                    </div>
                    <div className="historyDetails">
                        <div>:</div>
                        <div>:</div>
                        <div>:</div>
                        <div>:</div>
                    </div>
                    <div onClick={() => setIsShowingGraph(true)} className="historyDetails percentage">
                        <div className={percentOne > 0 ? "greenText" : "redText"}>{percentOne > 0 && "+"}{percentOne}%</div>
                        <div className={percentTwo > 0 ? "greenText" : "redText"}>{percentTwo > 0 && "+"}{percentTwo}%</div>
                        <div className={percentThree > 0 ? "greenText" : "redText"}>{percentThree > 0 && "+"}{percentThree}%</div>
                        <div className={percentFour > 0 ? "greenText" : "redText"}>{percentFour > 0 && "+"}{percentFour}%</div>
                    </div>
                </div>
            </div>
            {isShowingGraph && 
                <Graph 
                    key={props.id}
                    name={props.name} 
                    setIsShowingGraph={setIsShowingGraph} 
                    id={props.id}
                />
            }
        </div>
    )
}

export default Game