import React from "react"

import filterDownIcon from "../images/filter down.webp"
import filterUpIcon from "../images/filter up.webp"

function FilterTab(props) {

    return (
        <div className="filterTab">
            <div className="filterIcons">
                <img src={filterUpIcon}></img>
                <img src={filterDownIcon}></img>
            </div>
            <div className="filterInput"><input onChange={(e) => props.setSearchName(e.target.value)} className="gameSearch" placeholder="⌕ Search for game"></input></div>
        </div>
    )
}

export default FilterTab