import React, { useRef, useState } from 'react';
import ProviderList from "./ProviderLists"
import { Swiper, SwiperSlide } from 'swiper/react';

import 'swiper/css';
import 'swiper/css/pagination';

function Providers(props) {
    return (
        <div className="providers"> 
            <Swiper
                slidesPerView={4}
                spaceBetween={10}
                grabCursor={true}
                className="mySwiper"
            >
                {ProviderList.map(provider => {
                    return (
                        <SwiperSlide onClick={() => props.chooseProvider(provider.name)} className="providerContainer" key={provider.id}>
                            <div className='providerLogo'><img src={provider.logo}></img></div>
                            <div className='providerName'>{provider.name}</div>
                        </SwiperSlide>
                    )
                })}
            </Swiper>
        </div>
    );
}

export default Providers